const number = parseInt(prompt("Ï∞∏Í∞ÄÏûê"), 10);
const $input = document.querySelector("input"); // ÏûÖÎ†• field
const $order = document.querySelector('span[id="order"]'); // Ï∞∏Í∞ÄÏûê field
const $word = document.querySelector('span[id="word"]'); // Ï†úÏãúÏñ¥ field
const $listP = document.querySelector("p"); // Ï†úÏãúÏñ¥ field

let word; // Ï†úÏãúÏñ¥
let newWord; // Ïã†Í∑ú ÏûÖÎ†•
const wordArr = new Array(); // ÏûÖÎ†• Í∞í Ï†ÄÏû•

$input.addEventListener("input", (textData) => {
  newWord = textData.target.value; // Ïã†Í∑ú ÏûÖÎ†• Ï†ÄÏû•
});

const onButtonClick = () => {
  if (!word) {
    //Ï†úÏãúÏñ¥Í∞Ä ÎπÑÏñ¥ ÏûàÏùÑ Í≤ΩÏö∞
    word = newWord;
    $word.textContent = word; // Ï†úÏãúÏñ¥ Ï∂îÍ∞Ä
    wordArr.push(word); // ÏûÖÎ†•Í∞í Ï†ÄÏû•
    $input.value = ""; // ÏûÖÎ†• field Ï¥àÍ∏∞Ìôî
    $input.focus();

    //Ï∞∏Í∞ÄÏûê ÏàúÏÑúÎ•º Î≥ÄÍ≤Ω
    const order = parseInt($order.textContent);
    if (order >= number) {
      $order.textContent = 1;
    } else {
      $order.textContent = order + 1;
    }
    //ÏûÖÎ†• Î™©Î°ù
    $listP.textContent = wordArr.toString();
  } else {
    //Ï†úÏãúÏñ¥Í∞Ä ÎπÑÏñ¥ ÏûàÏßÄ ÏïäÏùÑ Í≤ΩÏö∞

    //Îã®Ïñ¥ ÎÅù Î∂ÄÎ∂Ñ ÎπÑÍµê
    if (wordArr.slice(-1).toString().slice(-1) === newWord.slice(0, 1)) {
      //Îã®Ïñ¥ ÎÅù Î∂ÄÎ∂ÑÏù¥ Í∞ôÏùÑ Í≤ΩÏö∞
      wordArr.push(newWord); // ÏûÖÎ†•Í∞í Ï†ÄÏû•
      $input.value = ""; // ÏûÖÎ†• field Ï¥àÍ∏∞Ìôî
      $input.focus();

      //Ï∞∏Í∞ÄÏûê ÏàúÏÑúÎ•º Î≥ÄÍ≤Ω
      const order = parseInt($order.textContent);
      if (order >= number) {
        $order.textContent = 1;
      } else {
        $order.textContent = order + 1;
      }
      //ÏûÖÎ†• Î™©Î°ù
      $listP.textContent = wordArr.toString();
    } else {
      //Îã®Ïñ¥ ÎÅù Î∂ÄÎ∂ÑÏù¥ Îã§Î•º Í≤ΩÏö∞
      alert("ÌãÄÎ¶º");
    }
  }
};
document
  .querySelector("button[class='btn']")
  .addEventListener("click", onButtonClick);
